.navbar {
    width: 100%;
    height: 80px;
    background-color: rgb(66, 38, 4);
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.links {
    margin-right: 35px;
    display: flex;
    align-items: center;
}

.links a {
    text-decoration: none;
    color: white;
    margin-left: 50px;
    font-size: 25px;
    font: bold 20px "Helvetica", sans-serif;
}